<template>
  <div class="about">
    {{ titlePage }}
    <form @submit.prevent="login">
      <input type="text" v-model="userform.user" />
      <input type="text" v-model="userform.pass" />
      <button type="submit">Login</button>
    </form>
  </div>
</template>
<script>
/* eslint-disable */
export default {
  name: "LoginView",
  data() {
    return {
      titlePage: "Login",
      math: Math.random(),
      userform: {
        user: "",
        form: "",
      },
    };
  },
  methods: {
    login() {
      this.$store.dispatch("login", this.userform).then((res) => {
        if (res != true) {
          alert("username atau password salah");
        } else {
          this.$router.push("/home");
        }
      });
    },
  },
  beforeCreate() {
    console.log("before create title " + this.titlePage + " " + Math.random());
  },
  created() {
    console.log("created title " + this.titlePage + " " + Math.random());
  },
  beforeMount() {
    console.log("before mount title " + this.titlePage + " " + Math.random());
  },
  mounted() {
    console.log("mounted title " + this.titlePage + " " + Math.random());
  },
  beforeUpdate() {
    console.log("before update title " + this.titlePage);
  },
  updated() {
    console.log("updated title " + this.titlePage);
  },
  beforeDestroy() {
    console.log("before destroy title " + this.titlePage);
  },
  destroyed() {
    console.log("destroyed title " + this.titlePage);
  },
};
</script>