import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    menu: [{
      id: 1,
      label: 'Home',
      route: '/home'
    }, {
      id: 2,
      label: 'About',
      route: '/about'
    }, {
      id: 3,
      label: 'Product',
      route: '/product'
    }, {
      id: 4,
      label: 'Contact',
      route: '/contact'
    }, {
      id: 5,
      label: 'Login',
      route: '/auth'
    }],
    user: '',
    sidebar: JSON.parse(localStorage.getItem('sidebar') || '{}'),
    total_cart: localStorage.getItem('cart') || 0
  },
  getters: {
    getMenu: state => state.menu
  },
  mutations: {
    setAuth(state, payload) {
      state.user = payload
    },
    setMenu(state, payload) {
      state.sidebar = payload
      localStorage.setItem('sidebar', payload)
    },
    setCart(state) {
      state.total_cart++
      localStorage.setItem('cart', state.total_cart)
    }
  },
  actions: {
    login({
      commit,
      dispatch
    }, payload) {
      if (payload.user == 'sa' && payload.pass == '123') {
        commit('setAuth', payload)
        dispatch('setSidebar')
        return true
      } else {
        return false
      }
    },
    setSidebar({
      commit
    }) {
      fetch("https://my-json-server.typicode.com/nuabdi310/database/menu")
        .then((res) => res.json())
        .then((res) => {
          commit('setMenu', JSON.stringify(res))
        });
    },
    addCart({
      commit
    }) {
      commit('setCart')
    }
  },
  modules: {}
})
