<template>
  <div class="banner">
    Lorem ipsum dolor sit : {{ math }} <br />
    <div v-if="isLoading">Loading...</div>
    <div v-else>
      {{ menu }}
    </div>
  </div>
</template>

<script>
export default {
  props: ["math"],
  data() {
    return {
      menu: [],
      isLoading: false,
    };
  },
  created() {
    this.isLoading = true;
    fetch("https://my-json-server.typicode.com/nuabdi310/database/menu")
      .then((res) => res.json())
      .then((res) => {
        this.isLoading = false;
        this.menu = res;
      });
    console.log("component banner created");
  },
};
</script>

<style>
.banner {
  height: 150px;
  border-radius: 10px;
  background-color: #eee;
  padding: 15px;
  overflow-y: scroll;
}
</style>